.transport__content {
  position: relative;
  display: block;
  width: 33px;
  height: 33px;
  border-radius: 50%;
  background-color: $white;
}

.transport__svg {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  fill: navy;
}
